.card {
  border-radius: 2px;
  margin: 3rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px rgb(0, 0, 0), 0 3px 6px #999;
  text-align: left;
  margin-top: 135px;
  margin-bottom: -75px;
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background: #e46c0a;
}
.card > .img-container > img {
  width: 60%;
}

img {
  height: 200px;
}

.title {
  font-size: 3rem;
  text-decoration: underline;
  color: #0ae4b8;
  font-family: "Rubik", sans-serif;
  text-shadow: 2px 2px 1px black;
}

.subText {
  font-size: 20px;
  color: white;
  text-align: center;
}

.upTop {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  background: #4f0358;
  padding: 10px;
  box-shadow: 1px 1px 1px rgb(53, 5, 68);
  position: fixed;
  top: 0;
  z-index: 1;
}

.wrapper {
  background-image: url(/Click-Game/static/media/background.0535c897.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #610ae4;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

